import React from "react";
import { Menu } from "antd";
import { Link } from "react-router-dom";
import { useTranslation } from 'react-i18next';

function NavMenu(props) {

  const { t } = useTranslation();

  return (
    props.links === undefined ? <div></div> : 
    (<Menu mode="horizontal" selectedKeys={[window.location.pathname]}>
      {props.links.map((link) => (
        <Menu.Item key={link.to} icon={link.icon}>
          <Link to={link.to}>{t("NavMenu." + String(link.display).toLocaleLowerCase(), link.display)}</Link>
        </Menu.Item>
      ))}
    </Menu>)
  );
}

export default NavMenu;
