import React, { useState } from "react";
import { Layout, Menu, Dropdown, Button, Col, Row } from "antd";
import { DownOutlined } from "@ant-design/icons";
import { useTranslation } from 'react-i18next';
import "antd/dist/antd.min.css";
import logo from "./assets/weg_logo.png";
import grid from "./assets/grid.png";
import styles from "./WegLayout.module.css";
import NavMenu from "./NavMenu";
import Bar from "./Bar";


const { Header, Content, Footer } = Layout;

export default function WegLayout(props) {

  const { t, i18n } = useTranslation();
  const [lngSetted, setlngSetted] = useState(String(i18n.language).toUpperCase());

  const menu = (
    <Menu>
      <Menu.Item>
        <Button size={"small"} type={'link'} onClick={() => changeLanguage("en")}>EN</Button>
      </Menu.Item>
      <Menu.Item>
        <Button size={"small"} type={'link'} onClick={() => changeLanguage("es")}>ES</Button>
      </Menu.Item>
      <Menu.Item>
        <Button size={"small"} type={'link'} onClick={() => changeLanguage("pt-br")}>PT-BR</Button>
      </Menu.Item>
    </Menu>
  );

  const changeLanguage = lng => {
    let upper_lng = String(lng).toUpperCase();
    setlngSetted(upper_lng);
    i18n.changeLanguage(lng);
  };

  return (
    <Layout className={styles.container}>
      <Header className={styles.header} title={props.appTitle}>
        <img src={logo} className={styles.logo} alt="WEG" />
        <span className="ant-page-header-heading-title">{props.appTitle}</span>
      </Header>
      <Content>
        <div>
          {props.links === undefined ? <div></div> : <NavMenu links={props.links} />}
          {props.title === undefined ? <div></div> : <Bar title={props.title}></Bar>}
        </div>
        <div>{props.children}</div>
      </Content>
      <Footer className={styles.footer}>
        <Row>
          <Col span={12}>
            {t("WegLayout.footer", "Powered by Engineering Systems Development Section | Maestro")} <br></br>
            <Dropdown overlay={menu} placement="topLeft">
              <Button type={'link'} style={{ fontSize: "100%", paddingLeft: "0" }} className="ant-dropdown-link" onClick={e => e.preventDefault()}>
                {lngSetted} <DownOutlined />
              </Button>
            </Dropdown>
          </Col>
          <Col span={12}>
            <img src={grid} className={styles.grid} alt="WEG" />
          </Col>
        </Row>
      </Footer>
    </Layout>
  );
}
