import { Spin, Select, Button, Row, Card, Modal } from "antd";
import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import WegLayout from "../../components/WegLayout";
import { exportDatabase } from "../../services/api";

function Home() {
  const { t } = useTranslation();
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState([]);
  const [selectedDatabase, setSelectedDatabase] = useState('');

  function onSelect(value) {
    setSelectedDatabase(value);
  }

  function postSchema() {
    setLoading(true);
    exportDatabase.postSchema(selectedDatabase)
      .then(res => {
        Modal.success({
          title: "Sucesso",
          okText: "Ok",
          content: "A base de dados foi copiado de produção para QA com sucesso!",
        });
      })
      .finally(() => setLoading(false));
  }

  useEffect(() => {
    const load = async () => {
      setLoading(true);
      exportDatabase.getData()
        .then(res => setData(res))
        .finally(() => setLoading(false));
    }
    load();
  }, [])

  return (
    <Spin spinning={loading}>
      <WegLayout appTitle={'Export Database Admin'} title={"Export Database"}>
        <Row style={{ width: '100%', height: 500, justifyContent: 'center' }}>
          <Card
            title={t("Home.card", "Realizar atualização")}
            style={{ width: '50%', alignSelf: 'center', backgroundColor: '#fafafa' }}
          >
            <Select
              style={{ width: '70%', marginRight: '1%' }}
              showSearch
              onSelect={onSelect}
              options={data?.map(value => { return { label: value, value: value } })}
            />
            <Button
              type="primary"
              onClick={postSchema}
              disabled={!selectedDatabase || loading}
            >
              {t("Home.button", "Realizar atualização")}
            </Button>
          </Card>
        </Row>
      </WegLayout>
    </Spin>
  );
}

export default Home;
