import React, { useState } from "react";
import { Modal, Spin, Row, Col } from "antd";
import { useNavigate } from "react-router-dom";
import { auth } from "../../services/api";
import LoginForm from "./LoginForm";
import styles from "./Login.module.css";
import gridImg from "./grid_white.png";

const INITIAL_VALUES = {
  title: "Export Database"
}

export default function Login() {
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();

  let convertObjToFormData = (obj) => {
    const formData = new FormData();
    Object.keys(obj).forEach((key) => formData.append(key, obj[key]));
    return formData;
  };

  let fetchLogin = (form) => {
    setLoading(true);
    auth.login(convertObjToFormData(form))
      .then((response) => {
        if (response.status === 200) {
          const newUrl = response.url?.split(window.location.href.split('/login')[0])[1] || '/';
          navigate(newUrl);
        }
      })
      .catch(err => {
        Modal.error({
          title: "Invalid credentials",
          okText: "Ok",
          content: "Please enter a valid username and password.",
        });
      })
      .finally(() => setLoading(false));
  };

  return (
    <Spin spinning={loading}>
      <div className={styles.container}>
        <Row style={{ width: "100%" }}>
          <Col span={14}>
            <Row align={"middle"} style={{ height: "100%", paddingLeft: '3%' }}>
              <img src={gridImg} className={styles.grid} alt="WEG" />
            </Row>
          </Col>
          <Col span={10}>
            <LoginForm title={INITIAL_VALUES.title} onSubmit={(form) => fetchLogin(form)} />
          </Col>
        </Row>
      </div>
    </Spin>
  );
}
