import React from "react";
import { Space, Typography } from "antd";
import styles from "./bar.module.css";

const { Title } = Typography;
function Bar(props) {

  return (
    <div className={styles.container}>
      <Space>
        <Title style={{ color: "#FFF" }} level={4} >{props.title}</Title>
      </Space>
    </div>
  );
}

export default Bar;
