import { Modal } from "antd";
import axios from "axios";

const endpoint = process.env.NODE_ENV === 'development' ? 'http://localhost:8080' : '';

const client = axios.create({
    baseURL: endpoint
});

client.interceptors.request.use(async (config) => {
    config.withCredentials = true;
    return config;
});

const handleAxiosException = function (err) {
    let message = err.message;
    if (err.response && err.response.status !== "200" && err.response.data?.message) {
        message = err.response.data.message;
    }
    Modal.error({
        title: "Server error",
        okText: "Ok",
        content: message,
    });
}

const auth = {
    async login(form) {
        try {
            let response = await client.post("/userAuth", form);
            return response;
        } catch (err) {
            console.log(err)
        }
    },
    async logout() {
        try {
            let response = await client.post("/logout", {});
            if (response.status === 200) {
                return true;
            }
        } catch (err) {
            return false;
        }
    }
}

const exportDatabase = {
    async getData() {
        try {
            let response = await client.get("/front/admin/database/schema/all");
            return response.data;
        } catch (err) {
            handleAxiosException(err);
        }
    },

    async postSchema(schema) {
        try {
            let response = await client.post(`/front/admin/database/schema/${schema}`);
            return response.data;
        } catch (err) {
            handleAxiosException(err);
        }
    },

};

export { endpoint, auth, exportDatabase };